<template>
  <van-grid :border="false" :column-num="2" :gutter="4" :center="false">
    <van-grid-item
      v-for="(item, index) in bookitem"
      :key="item.id"
      @click="getBookData(item)"
    >
      <img
        width="100%"
        :src="
          'https://image.zymkcdn.com/file/cover/' +
          item.comic_id +
          '_2_1.jpg-720x360.jpg.webp'
        "
      />

      <div class="binpai">
        <LabelType>{{ item.comic_type[0] }}</LabelType>
        <p>{{ item.comic_name }}</p>
      </div>

      <h3>{{ item.content }}</h3>
      <div class="hotbox">
        <van-icon name="fire-o" size="13" />
        <span> {{ hotnum[index] }}</span>
      </div>
    </van-grid-item>
  </van-grid>
</template>

<script>
import LabelType from "@/components/LabelType.vue";
// import { mapState } from 'vuex';

export default {
  props: ["bookitem"],
  computed: {
    hotnum: function () {
      return this.bookitem.map((item) => {
        var ind = item.total_view_num;

        var k = 10000,
          sizes = ["", "万"],
          i;
        if (ind < k) {
          return ind;
        } else if (ind > 100000000) {
          ind = (ind / 100000000).toFixed(1) + "亿";
        } else {
          i = Math.floor(Math.log(ind) / Math.log(k));

          ind = (ind / Math.pow(k, i)).toFixed(0) + sizes[i];
        }

        return ind;
      });
    },
  },
  methods: {
    getBookData(item) {
      this.$store.dispatch("getBookItemData",item.id);
      this.$router.push({ path: "/book", query: { id: item.comic_id } });
    },
  },
  components: {
    LabelType,
  },
};
</script>

<style scoped lang="less">
.van-swipe__track {
  .binpai {
    display: flex;
    align-items: center;
    padding-top: 8px;
    padding-bottom: 4px;

    p {
      margin: 0;
      font-size: 16px;

      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
    }
  }
  h3 {
    margin: 0;
    padding-right: 12px;

    color: #999;
    font-size: 14px;
    font-weight: normal;
    text-align: left;

    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }
  .hotbox {
    position: absolute;
    top: 8px;
    right: 4px;

    padding: 0 5px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 14px 0 0 14px;
    color: white;
    font-size: 13px;
  }
}
</style>