<template>
  <div class="lbtbox">
    <van-swipe @change="onLbtChange" autoplay="3000">
      <van-swipe-item v-for="(item,index) in lbtdata" :key="item.id" @click="gobook(item,urlid[index])">
          <img :src="'https://image.zymkcdn.com/file/news/'+urlid[index]+'.jpg'" />
      </van-swipe-item>


      <template #indicator>
        <div class="custom-indicator">
            <ul>
                <li v-for="(item,index) in lbtdata" :key="item.id">
                    <van-icon size="16" :color="current != index ? 'rgba(0, 0, 0, 0.5)':'#fff'" name="fire" />
                </li>
            </ul>
        </div>
      </template>
    </van-swipe>
  </div>
</template>

<script>
export default {
  props: ["lbtdata"],
  data() {
    return {
      current: 0,
    };
  },
  methods: {
    onLbtChange(index) {
      this.current = index;
    },
    gobook(obj,id){
      this.$store.dispatch("getBookItemData", obj.comic_id);
      this.$router.push({
        path: "/book",
        query: { id },
      });
    }
  },
  computed: {
      urlid(){
          return this.lbtdata.map(item=>{
              var id = (item.id/1000).toFixed(3).split(".");
              var ids = (id[0]/1000).toFixed(3).split(".");
              if(item.id<1000){
                  id = "000/000/" + id[1];
              }else if(item.id<1000000){
                //   var arr = (item.id/1000).toFixed(3).split(".")[1]
                  id = "000/" + ids[1] +"/"+ id[1];
              }else{
                  id = (ids[0]/1000).toFixed(3).split(".")[1] +"/"+ ids[1] +"/"+ id[1];
              }
              return id;
          });
      }
  }
};
</script>

<style lang="less">
.lbtbox{
    img{
        width: 100%;
    }
}

.custom-indicator {
  position: absolute;
  right: 5px;
  bottom: 4%;
  padding: 2px 5px;
  font-size: 12px;

  ul{
      display: flex;
      li{
          margin: 0 3px;
        //   background: rgba(0, 0, 0, 0.5);
      }
  }
}
</style>