<template>
  <div class="bookslide">
    <!-- 标题 -->
    <van-nav-bar :title="books.title" :border="false">
      <template #right>
        <div class="titlebox-right">
          <van-icon name="more" color="#d8d8d8" />
          <span>更多</span>
        </div>
      </template>
    </van-nav-bar>

    <!-- 可滑动区域 -->
    <van-swipe class="my-swipe" :show-indicators="false">
      <van-swipe-item v-for="(item, index) in booklist" :key="index">
        <!-- <ul class="sixbox bookbox">
          <li v-for="cont in item" :key="cont.comic_id">
            {{ cont.comic_name }}
          </li>
        </ul> -->
        <BookSlideItemOne :bookitem="item" v-if="item.length != 5" />
        <BookSlideItemTwo :bookitem="item" v-else />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { mapState } from "vuex";
import BookSlideItemOne from "@/components/BookSlideItemOne.vue";
import BookSlideItemTwo from "@/components/BookSlideItemTwo.vue";

export default {
  props: ["booksindex"],
  data() {
    return {
      booklist: [],
    };
  },
  computed: {
    ...mapState({
      books: function (state) {
        return state.homedata[this.booksindex];
      },
    }),
  },
  methods: {},
  components: {
    BookSlideItemOne,
    BookSlideItemTwo,
  },
  created() {
    this.$store.commit("setbookid", this.booksindex);

    // 把数据分组，能不能凑多页
    // console.log(this.booksdata.config.display_type);
    switch (this.books.config.display_type) {
      case "32":
        if (this.books.comic_info.length < 15) {
          this.booklist.push(this.books.comic_info.slice(0, 5));
        } else {
          for (var i = 0; i < 3; i++) {
            this.booklist.push(
              this.books.comic_info.slice(0 + i * 5, 5 + i * 5)
            );
          }
        }
        break;

      case "6":
        if (this.books.comic_info.length < 15) {
          this.booklist.push(this.books.comic_info.slice(0, 5));
        } else {
          for (var j = 0; j < 3; j++) {
            this.booklist.push(
              this.books.comic_info.slice(0 + j * 5, 5 + j * 5)
            );
          }
        }
        break;

      case "0":
        if (this.books.comic_info.length < 4) {
          this.booklist = this.books.comic_info;
        } else {
          this.booklist.push(this.books.comic_info.slice(0, 4));
        }
        break;

      default:
        if (this.books.comic_info.length < 18) {
          this.booklist.push(this.books.comic_info.slice(0, 6));
        } else {
          for (var k = 0; k < 3; k++) {
            this.booklist.push(
              this.books.comic_info.slice(0 + k * 6, 6 + k * 6)
            );
          }
        }
        break;
    }
  },
};
</script>

<style lang="less">
.bookslide {
  margin-bottom: 24px;

  .van-nav-bar .van-nav-bar__content {
    // 中间标题
    .van-nav-bar__title {
      font-size: 19px;
      transform: translateX(-8%);
    }
    .van-ellipsis {
      overflow: inherit;
      white-space: nowrap;
      text-overflow: inherit;
    }

    // 右侧更多盒子
    .van-nav-bar__right {
      padding: 0 1vw;
    }
    // 右侧更多盒子
    .titlebox-right {
      color: rgb(167, 167, 167);
      font-size: 12px;
      border: 1px solid rgba(192, 192, 192, 0.7);
      border-radius: 16px;

      transform: scale(0.9);
      display: flex;
      align-items: center;
      padding: 0 6px;
      span {
        padding-left: 4px;
      }
    }
  }

  // 可滑动区域
  .my-swipe {
    .van-swipe-item {
      color: #333;
      font-size: 20px;
      padding: 4px 0;

      .van-grid-item__content {
        padding: 0;
      }
      .van-grid-item{
        margin-bottom: 12px;
      }
    }
  }
}
</style>