import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {shownav: true},
  },
  {
    path: '/classification',
    name: 'Classification',
    component: () => import(/* webpackChunkName: "about" */ '../views/Classification.vue'),
    meta: {shownav: true},
  },
  {
    path: '/find',
    name: 'Find',
    component: () => import(/* webpackChunkName: "about" */ '../views/Find.vue'),
    meta: {shownav: true},
  },
  {
    path: '/mydate',
    name: 'Mydate',
    component: () => import(/* webpackChunkName: "about" */ '../views/Mydate.vue'),
    meta: {shownav: true},
  },
  {
    path: '/book',
    component: () => import(/* webpackChunkName: "about" */ '../views/BookPage.vue'),
  },
  {
    path: "/lookchapter",
    name: "lookcartoon",
    component: () => import(/* webpackChunkName: "about" */ '../views/LookCartoon.vue'),
  },
  {
    path: "/paihang",
    name: "ranking",
    component: () => import(/* webpackChunkName: "about" */ '../views/Ranking.vue'),
  },
  {
    path: "/search",
    name: "search",
    component: () => import(/* webpackChunkName: "about" */ '../views/SearchPage.vue'),
  },
  {
    path: "/result",
    name: "result",
    component: () => import(/* webpackChunkName: "about" */ '../components/SearchResult.vue'),
  },
]

const router = new VueRouter({
  routes
})

router.afterEach((to) => {
  if(to.path == "/book") window.scrollTo(0, 0);
})

export default router
