<script>
import HomeLeftPage from "@/views/HomeLeftPage";
import HomeRightPage from "@/views/HomeRightPage";
import { mapState } from "vuex";

export default {
  props: ["isshowclass", "istglpage", "cpid", "shownum"],
  data() {
    return {
      // leftEndScroll: 0,
      // rightEndScroll: 0,
      rightLbt: [],
      rightData: [],
    };
  },
  computed: {
    ...mapState({
      homedatalength: (state) => state.homedata.length,
    }),
  },
  methods: {
    clkLeft() {
      this.$emit("update:istglpage", !this.istglpage);
      this.$emit("update:isshowclass", false);
      this.$emit("update:cpid", "HomeLeftPage");
    },
    clkRight() {
      this.axios
        .get(
          "https://getcomicinfo-globalapi.zymk.cn/app_api/v5/getupdatecomic_list/?client-type=android&productname=zymk&platformname=android&client-channel=store_huawei&loglevel=3&client-version=5.9.0"
        )
        .then((response) => {
          this.rightLbt = response.data.data.slidelist;
          this.rightData = response.data.data.update_list;
        });

      this.$emit("update:istglpage", !this.istglpage);
      this.$emit("update:isshowclass", true);
      this.$emit("update:cpid", "HomeRightPage");
    },
  },
  components: {
    HomeLeftPage,
    HomeRightPage,
  },
  // mounted() {
