<template>
  <div id="app">
    <keep-alive>
      <router-view
        v-bind:isshowclass.sync="isshowclass"
        v-bind:istglpage.sync="istglpage"
        v-bind:cpid.sync="cpid"
        :shownum="shownum"
      />
    </keep-alive>

    <FixedBtm v-show="$route.meta.shownav"></FixedBtm>
  </div>
</template>

<script>
import FixedBtm from "@/views/FixedBtm.vue";
// import {mapState} from 'vuex'

export default {
  data() {
    return {
      isshowclass: false,
      istglpage: false,
      cpid: "HomeLeftPage",
      shownum: 3,
    };
  },
  computed: {},
  components: {
    FixedBtm,
  },
  created() {
    window.addEventListener("scroll", () => {
      // console.log(this.$route);

      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      var scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;

      if (this.$route.name == "Home") {
        if (scrollTop >= 300) {
          this.isshowclass = true;
        } else if (scrollTop == 0 && !this.istglpage) {
          this.isshowclass = false;
        }

        if (scrollTop == scrollHeight - window.innerHeight) {
          this.shownum = this.shownum + 3 > 20 ? 20 : this.shownum + 3;
        }
      } else if (this.$route.path == "/book") {
        if (scrollTop > 200) {
          this.$store.commit("setShowbg", false);
        } else {
          this.$store.commit("setShowbg", true);
        }
      }
    });
  },
};
</script>

<style>
#app {
  font-family: SimHei, Microsoft Yahei, SimSun, Tahoma, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>
