<template>
  <li class="updateitem" @click="tobookin">
    <div class="imger">
      <img width="100%" v-lazy="imgurl" />
      <span><van-icon name="fire-o" />{{ cpdNum(itemdate.hot) }}</span>
    </div>
    <div class="headH">
      <h4>
        {{ itemdate.comic_name }}
        <LabelType :color="randerColor">{{
          itemdate.comic_type[0].name
        }}</LabelType>
      </h4>
      <p class="rig-p">{{ itemdate.author_name }}</p>
    </div>
    <div class="mainCont">
      <p>{{ itemdate.comic_feature }}</p>
      <p class="rig-p">
        更新至<span>{{ itemdate.last_chapter.name }}</span>
      </p>
    </div>
  </li>
</template>

<script>
import LabelType from "@/components/LabelType.vue";

export default {
  props: ["itemdate"],
  computed: {
    imgurl() {
      return (
        "https://image.zymkcdn.com/file/cover/" +
        this.setuid(this.itemdate.comic_id) +
        "_2_1.jpg-720x360.jpg.webp"
      );
    },
    randerColor() {
      return "#" + Math.random().toString(16).substr(2, 6).toUpperCase();
    },
  },
  methods: {
    // 计算函数 -------
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    cpdNum(ind) {
      var k = 100000,
        sizes = ["", "万"],
        i;
      if (ind < k) {
        return ind;
      } else if (ind > 100000000) {
        ind = (ind / 100000000).toFixed(1) + "亿";
      } else {
        i = Math.floor(Math.log(ind) / Math.log(10000));

        ind = ind / Math.pow(10000, i);
        if (ind < 100) {
          ind = ind.toFixed(1) + sizes[i];
        } else {
          ind = ind.toFixed(0) + sizes[i];
        }
      }

      return ind;
    },
    tobookin() {
      this.$store.dispatch("getBookItemData", this.itemdate.comic_id);
      this.$router.push({
        path: "/book",
        query: { id: this.setuid(this.itemdate.comic_id) },
      });
    },
  },
  components: {
    LabelType,
  },
};
</script>

<style lang="less">
.updateitem {
  p,
  h4 {
    margin: 0;
    padding: 0;
    font-weight: 400;
  }
  .imger {
    position: relative;
    font-size: 0;
    margin-bottom: 7px;

    > span {
      position: absolute;
      right: 3%;
      bottom: 5%;

      padding: 0 11px;
      background-color: rgba(0, 0, 0, 0.5);
      border-radius: 26px;
      transform: scale(0.9);

      color: white;
      font-size: 12px;
      line-height: 21px;

      i {
        transform: translate(-4px, 2px);
      }
    }
  }

  .headH,
  .mainCont {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
  }

  .headH {
    font-size: 17px;
    margin-bottom: 5px;
    white-space: nowrap;

    h4 {
      font-family: "微软雅黑";

      .labeltype {
        display: inline-block;
        transform: scale(0.8, 0.8) translateY(-1px);
      }
    }
    p {
      font-size: 15px;
      color: #999;
      overflow: hidden;
    }
  }
  .mainCont {
    color: #999;
    font-size: 12px;
    padding-bottom: 10px;
    white-space: nowrap;

    p:first-child {
      flex-flow: 6;
      //   transform-origin: left center;
      //   transform: scale(1.1);
    }
    p:last-child {
      flex-flow: 4;
      padding-left: 4px;
      overflow: hidden;
      //   transform-origin: right center;
      //   transform: scale(1.1);
      span {
        color: #fc796b;
      }
    }
  }
}
</style>