<template>
  <div class="bookslideitemtwo">
    <ul>
      <li v-for="(item, index) in bookitem" :key="item.id" @click="getBookData(item)">
        <div class="imgbox">
          <img
            width="100%"
            :src="
              'https://image.zymkcdn.com/file/cover/' +
              item.comic_id +
              '_3_4.jpg-300x400.jpg.webp'
            "
          />
        </div>

        <h3>{{ item.comic_name }}</h3>
        <div class="hotbox">
          <van-icon name="fire-o" size="13" />
          <span> {{ hotnum[index] }}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
// import LabelType from "@/components/LabelType.vue";

export default {
  props: ["bookitem"],
  computed: {
    hotnum: function () {
      return this.bookitem.map((item) => {
        var ind = item.total_view_num;

        var k = 10000,
          sizes = ["", "万"],
          i;
        if (ind < k) {
          return ind;
        } else if (ind > 100000000) {
          ind = (ind / 100000000).toFixed(1) + "亿";
        } else {
          i = Math.floor(Math.log(ind) / Math.log(k));

          ind = (ind / Math.pow(k, i)).toFixed(0) + sizes[i];
        }

        return ind;
      });
    },
  },
  methods: {
    getBookData(item) {
      this.$router.push({ path: "/book", query: { id: item.comic_id } });
      this.$store.dispatch("getBookItemData",item.id);
    },
  },
  components: {
    // LabelType,
  },
};
</script>

<style scoped lang="less">
.bookslideitemtwo {
  ul {
    font-size: 13px;
    overflow: hidden;

    li {
      float: left;
      width: 48vw;
      height: 100%;
      margin: 0 1vw;
      text-align: left;

      &:nth-child(n + 2) {
        width: 23vw;
        height: 50%;
        margin-bottom: 4.5vw;

        .imgbox {
          height: 23vw;
          overflow: hidden;
        }
      }

      h3 {
        margin-top: 10px;
        margin-bottom: 6px;
        font-weight: 400;
        white-space: nowrap;
        overflow: hidden;
      }
      .hotbox {
        color: #999;
        display: flex;
        align-items: center;
        span {
          padding-left: 4px;
        }
      }
    }
  }
}
</style>